main{
    background-color:rgb(255, 255, 255);
    max-width: 100rem;
    width: 96%;   
    margin: 8rem auto;
    text-align: center;
    border-radius: 2rem;
    border: 1px solid #acacb8;
}
section{
    padding: 2rem 2rem ;
}
section h1{
    font-size: 4.5rem;
    margin-bottom: 3rem;
    font-family: 'Satisfy', cursive;
    font-weight: 100;
    color: #32334f;
}
section p{
    line-height : 30px;

    margin-bottom: 4rem;
    font-size: 1.8rem;
    color: #32334f;
}